<template>
<div class="star">
  <div class="starwrapper">
  </div>
  <Row>
    <Col span="3">
      <span class="typelabel">量表类型</span>
    </Col>
    <Col span="4">
      <Select placeholder="选择类型" size="small" v-model="startype">
          <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
      </Select>
    </Col>
    <!-- <Col offset="2" span="2">
      <span>程度</span>
    </Col>
    <Col span="3" class-name="textarea11">
       <Input v-model="min" type="textarea" :autosize="true"></Input>
    </Col>
    <Col  offset="1"  span="1">
       <span class="splitesign">----</span>
    </Col>
    <Col span="3" class-name="textarea11">
       <Input v-model="max" type="textarea" :autosize="true"></Input>
    </Col> -->
    <Col offset="1" span="3">
      <span class="typelabel">量表范围</span>
    </Col>
     <Col span="1">
      <numberinput @numinfo="getrange"></numberinput>
    </Col>
  </Row>
</div>
</template>

<script type="text/ecmascript-6">
import numberinput from './numberinput1.vue'
import divtextarea from './divtextarea.vue'
import addchoose from './addchoose.vue'
export default {
  components: {
    'divtextarea': divtextarea,
    'addchoose': addchoose,
    'numberinput': numberinput
  },
  methods: {
    getrange (range) {
      // console.log(range)
      this.$store.commit('savesrange', range)
    }
  },
  data () {
    return {
      startype: '满意度',
      cityList: [
        { value: '满意度', label: '满意度' },
        { value: '认同度', label: '认同度' },
        { value: '符合度', label: '符合度' }
      ],
      high: ''
    }
  },
  watch: {
    startype: function (val) {
      this.$store.commit('savestype', val)
      // console.log(this.startype)
    }
  },
  created () {
    // console.log(this.$store.getters.getcontainer)
    if (this.$store.getters.getisediting) {
      var questions = this.$store.getters.getquestions
      var id = this.$store.getters.geteditindex
      this.startype = questions[id].question.stype
      // console.log(this.startype)
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.starwrapper
  margin-left 37px
  margin-top 18px
.closesort
  width 10px
  vertical-align middle
  margin-left 12px
.typelabel
  vertical-align middle
.splitesign
  position relative
  right 10px
.inputclass
  width 100%
  border 1px solid #dadada
  position relative
  right 15px
.textarea11
  position relative
  bottom 5px
</style>



