<template>
  <div class="dropdown">
    <div class="dropdownwrapper">
      <div class="listwrapper">
        <div class="row">
          <label>第一级</label>
          <label>第二级</label>
          <!-- <div class="numwrapper">
            <numberinput  @numinfo="getoptionnum"></numberinput>
          </div> -->
        </div>
        <div v-for="(option, index) in options" class="options">
          <div class="option1 row2">
            <Input @on-change="sendopt" v-model="option.option" type="textarea" :autosize="true" placeholder="第一级选项"></Input>
            <img @click="deleteop(index)" class="close" src="./close1.png">
          </div>
          <div class="option2">
            <div v-for="(suboption, index2) in option.suboptions" class="row">
              <Input @on-change="sendopt" v-model="suboption.suboption" type="textarea" :autosize="true" placeholder="第二级选项"></Input>
              <img @click="deleteop1(index, index2)" class="close" src="./close1.png">
            </div>
            <div class="row"  @click="clickaddop1(index)">
              <img class="choosesign" src="./add.png">
              <addchoose>添加第二级选项</addchoose>
              <div class="addclose"></div>
            </div>
          </div>
        </div>
        <div class="row"  @click="clickaddop">
          <img class="choosesign" src="./add.png">
          <addchoose>添加第一级选项</addchoose>
          <div class="addclose"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import addchoose from './addchoose.vue'
import numberinput from './numberinput1.vue'
export default {
  components: {
    'addchoose': addchoose,
    'numberinput': numberinput
  },
  data () {
    return {
      optionnum: '',
      problemnum: '',
      options: [
        // { 
        //   option: '',
        //   suboptions: [{
        //     suboption: ''
        //   }] 
        // },
        { 
          option: '',
          suboptions: [{
            suboption: ''
          }]
        }
      ],
      options1: []
    }
  },
  created () {
    console.log('safdjkls;sdfkl;')
    if (this.$store.getters.getisediting) {
      var arr = []
      var obj = this.$store.getters.getoptions
      for (var i in obj) {
        arr.push(obj[i])
      }
      var data = []
      var n1 = -1
      for (var key in arr) {
        //console.log(options[key].option)
        var o = arr[key].option.split('-')
        if ((n1 === -1) || (o[0] != data[n1].option)) { 
          n1++
          data[n1] = {
            option: o[0],
            suboptions: []
          }
          // data[n1].children[0] = o[2]
        }
        data[n1].suboptions.push({
          suboption: o[1]
        })
      }
      return this.options = data   
    }
  },
  methods: {
    refresh () {
    // console.log('safdjkls;sdfkl;')
      var arr = []
      var obj = this.$store.getters.getoptions
      // console.log(obj)
      for (var i in obj) {
        arr.push(obj[i])
      }
      var data = []
      var n1 = -1
      for (var key in arr) {
        //console.log(options[key].option)
        var o = arr[key].option.split('-')
        if ((n1 === -1) || (o[0] != data[n1].option)) { 
          n1++
          data[n1] = {
            option: o[0],
            suboptions: []
          }
          // data[n1].children[0] = o[2]
        }
        data[n1].suboptions.push({
          suboption: o[1]
        })
      this.options = data   
    }
    },
    sendopt () {
      this.options1.length = 0
      for (var i = 0; i < this.options.length; i++) {
        for (var j = 0; j < this.options[i].suboptions.length; j++) {
          this.options1.push({ option: this.options[i].option + '-' + this.options[i].suboptions[j].suboption })
        }
      }
      this.$store.commit('saveoption', this.options1)
      // console.log(this.options1)
      // this.$store.commit('saveoption', this.options)
    },
    clickaddop () {
      this.options.push({ option: '', suboptions: [{ suboption: '' }] })
    },
    clickaddop1 (index) {
      this.options[index].suboptions.push({ suboption: '' })
    },
    deleteop (index) {
      this.options.splice(index, 1)
      this.options1.length = 0
      this.options[index].suboptions.splice(index2, 1)
      for (var i = 0; i < this.options.length; i++) {
        for (var j = 0; j < this.options[i].suboptions.length; j++) {
          this.options1.push({ option: this.options[i].option + '-' + this.options[i].suboptions[j].suboption })
        }
      }
      this.$store.commit('saveoption', this.options1)
      // this.$store.commit('saveoption', this.options)
    },
    deleteop1 (index, index2) {
      this.options1.length = 0
      this.options[index].suboptions.splice(index2, 1)
      for (var i = 0; i < this.options.length; i++) {
        for (var j = 0; j < this.options[i].suboptions.length; j++) {
          this.options1.push({ option: this.options[i].option + '-' + this.options[i].suboptions[j].suboption })
        }
      }
      this.$store.commit('saveoption', this.options1)
      // console.log(this.options1)
      // this.$store.commit('saveoption', this.options)
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.dropdown
  margin-top 18px
.dropdownwrapper
  display flex
.listwrapper
  width 100%
.options
  width 100%
  overflow hidden
  height auto
.option1
  width 50%
  // margin-left 38px
  box-sizing border-box
  float left
.option2
  width 50%
  box-sizing border-box
  float right
.row2
  min-height 26px
  box-sizing border-box
  margin-bottom 10px
  display flex
  width 50%
.row
  min-height 26px
  box-sizing border-box
  margin-bottom 10px
  display flex
  width 100%
.row label
  width 50%
  vertical-align middle
  line-height 26px
.row .numwrapper
  margin-top 4px
.close
  width 25px
  height 25px
  display block
  margin-left 5px
  margin-right 5px
  padding 7px
  box-sizing border-box
  cursor pointer
  float left
.addclose
  width 52px
.choosesign
  width 25px
  height 25px
  display block
  // margin-left 5px
  margin-right 7px
  padding 5px
  box-sizing border-box
  cursor pointer
</style>



