<template>
  <div class="choose">
  <div v-for="(option, index) in options" class="chooserow">
    <img class="choosesign" src="./circle2.png">
    <Input @on-change="send" v-model="option.option" type="textarea" :autosize="true" placeholder="选项"></Input>
    <img class="chooseclose" src="./close1.png"  @click="deleteop(index)">
  </div>
  <div class="chooserow" @click="clickadd">
    <img class="choosesign" src="./add.png">
    <addchoose>添加选项</addchoose>
    <div class="addclose"></div>
  </div>
 <!--  <div v-for="(editoption, index) in editoptions" class="chooserow">
    <img class="choosesign" src="./circle2.png">
    <Input v-model="editoption.option"  type="textarea" :autosize="true" placeholder="其他项____"></Input>
    <img class="chooseclose" src="./close1.png" @click="deleteeditop(index)">
  </div> -->
  <!-- <div class="chooserow" @click="clickaddedit">
    <img class="choosesign" src="./add.png">
    <addchoose>添加其他项</addchoose>
    <div class="addclose"></div>
  </div> -->
  </div>
</template>

<script type="text/ecmascript-6">
import addchoose from './addchoose.vue'
export default {
  components: {
    'addchoose': addchoose
  },
  data () {
    return {
      options: [
        {option: ''}
      ],
      editoptions: [
        {option: ''}
      ]
    }
  },
  methods: {
    refresh () {
      var arr = []
      var obj = this.$store.getters.getoptions
      for (var i in obj) {
        arr.push(obj[i])
      }
      this.options = arr
    },
    send () {
      this.$store.commit('saveoption', this.options)
    },
    clickadd () {
      this.options.push({option: ''})
    },
    clickaddedit () {
      this.editoptions.push({option: ''})
    },
    deleteop (index) {
      this.options.splice(index, 1)
      this.$store.commit('saveoption', this.options)
    },
    deleteeditop (index) {
      this.editoptions.splice(index, 1)
    }
  },
  created () {
    if (this.$store.getters.getisediting) {
      var arr = []
      var obj = this.$store.getters.getoptions
      for (var i in obj) {
        arr.push(obj[i])
      }
      this.options = arr
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.choose
  margin-top 18px
.chooserow
  width 53%
  display flex
  margin-bottom 10px
.chooseclose
  width 25px
  height 25px
  display block
  margin-left 10px
  padding 7px
  box-sizing border-box
  cursor pointer
.choosesign
  width 25px
  height 25px
  display block
  margin-left 5px
  margin-right 7px
  padding 5px
  box-sizing border-box
  cursor pointer
.addclose
  width 45px
</style>



