<template>
  <div class="content1list">
    <ul>
      <li @click="showcontainer('0')"><button><img src="./choose1.png"><span>单选题</span></button></li>
      <li @click="showcontainer('1')"><button><img src="./chooosesquare1.png"><span>多选题</span></button></li>
      <li @click="showcontainer('10')"><button><img src="./dropdown.png"><span>下拉题</span></button></li>
      <!-- <li @click="showcontainer('2')"><button><img src="./parenthese1.png"><span>填空题</span></button></li> -->
      <li @click="showcontainer('3')"><button><img src="./test11.png"><span>单行文本题</span></button></li>
      <li @click="showcontainer('4')"><button><img src="./tests11.png"><span>多行文本题</span></button></li>
      <li @click="showcontainer('5')"><button><img src="./scale1.png"><span>量表题</span></button></li>
      <li @click="showcontainer('6')"><button><img src="./sort1.png"><span>排序题</span></button></li>
<!--       <li @click="showcontainer('7')"><button><img src="./single11.png"><span>矩阵单选题</span></button></li>
      <li @click="showcontainer('8')"><button><img src="./notsingle11.png"><span>矩阵多选题</span></button></li>
      <li @click="showcontainer('9')"><button><img src="./scales1.png"><span>矩阵量表题</span></button></li>
 -->
<!--       <li><button><img src="./connected.png"><span>联动题</span></button></li>
      <li><button><img src="./add.png"><span>附件题</span></button></li>
      <li><button><img src="./explain.png"><span>段落说明</span></button></li>
      <li><button><img src="./write.png"><span>填空题</span></button></li> -->
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {
      fristsave: false
    }
  },
  methods: {
    showcontainer (type) {
      // if (this.$store.getters.getname === '') {
      //   this.$Notice.warning({
      //     title: '先填写问卷标题哦',
      //     desc: '填写完问卷标题才能，发布问卷哦！',
      //     top: 200
      //   })
      // } else {
        if (this.iscontainershow || this.isediting) {
          this.$Message.warning('请完成当前题目')
        } else {
          this.$store.commit('changecontainertype', type)
          this.$store.commit('showcontainer')
        }
      // }
    }
  },
  computed: {
    iscontainershow () {
      return this.$store.getters.getcontainershow
    },
    isediting () {
      return this.$store.getters.getisediting
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.content1list button
  box-sizing border-box
  background-color white
  border 1px solid rgba(0,0,0,0.1)
  text-align left
  margin 3px
  padding 2px
  width 98%
  height 40px
  color #505050
  cursor pointer
.content1list button:focus
  outline none
.content1list img
  width 16px
  vertical-align middle
  margin-left 10px
.content1list
  overflow-x hidden
.content1list span
  vertical-align middle
  margin-left 10px
</style>



