<template>
  <div v-if="isviewshow" class="surveyview">
    <div  @click="closeview" class="closeimgwrapper">
      <img src="./closebtn.png">
    </div>
    <div class="viewwrapper">
      <div class="viewinner">
        <surveylist></surveylist>
      </div>
      <div class="publish1">
        <img src="./plane.png">
        <span>发布问卷</span>
      </div>
      <div @click="closeview" class="publish2">
        <img src="./comeback.png">
        <span>返回编辑</span>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import surveylist from '../questionlist/newquestionlist'
export default {
  components: { surveylist },
  props: {
    isviewshow: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    closeview () {
      this.$emit('view-close')
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.surveyview
  position fixed
  height 100%
  width 100%
  background-color #f3f4f6
  z-index 1000
  overflow-y auto
.viewwrapper
  background-color white
  border 1px solid rgba(0,0,0,0.1)
  min-height 1000px
  width 45%
  margin 100px auto
  position relative
.closeimgwrapper
  position absolute
  right 60px
  top 40px
  cursor pointer
.closeimgwrapper img
  width 30px
.viewinner
  box-sizing border-box
  padding 30px 50px
.publish1
  position absolute
  top 0px
  right -130px
  top 80px
  display flex
.publish1 img
  width 30px
  height 30px
.publish2
  position absolute
  top 0px
  right -130px
  top 30px
  display flex
.publish2 img
  width 30px
  height 30px
.publish1 span, .publish2 span
  display inline-block
  line-height 30px
  vertical-align middle
  margin-left 15px
  font-size 13px
.publish1, .publish2
  cursor pointer
</style>



