<template>
  <div class="sort">
  <div v-for="(option, index) in options">
    <Row class-name="textarea22">
      <Col span="23">
        <Input @on-change="send" v-model="option.option" type="textarea" :autosize="true" placeholder="选项"></Input>
      </Col>
      <Col span="1">
        <img @click="deleteop(index)" class="closesort" src="./close1.png">
      </Col>
    </Row>
  </div>
  <Row>
      <Col span="23">
      <div @click="clickadd">
        <addchoose>+添加选项</addchoose>
      </div>
      </Col>
    </Row>
  </div>
</template>

<script type="text/ecmascript-6">
import addchoose from './addchoose.vue'
export default {
  components: {
    'addchoose': addchoose
  },
  data () {
    return {
      options: [
        {option: ''}
      ]
    }
  },
  methods: {
    refesh() {
      var arr = []
      var obj = this.$store.getters.getoptions
      for (var i in obj) {
        arr.push(obj[i])
      }
      this.options = arr    
    },
    clickadd () {
      this.options.push({option: ''})
    },
    deleteop (index) {
      this.options.splice(index, 1)
      this.$store.commit('saveoption', this.options)
    },
    send () {
      this.$store.commit('saveoption', this.options)
    }
  },
  created () {
    if (this.$store.getters.getisediting) {
      var arr = []
      var obj = this.$store.getters.getoptions
      for (var i in obj) {
        arr.push(obj[i])
      }
      this.options = arr
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.sort
  margin-left 37px
  margin-top 18px
.closesort
  width 10px
  vertical-align middle
  margin-left 7px
  cursor pointer
.textarea22
  margin-bottom 7px
.textarea22 img
  position relative
  top 3px
</style>



