<!-- 如需使用类似 <numberinput @numinfo="getmax所调用父元素中方法"></numberinput> -->
<template>
  <div class="numberinput">
  <input @keyup="sendnum" v-model="inputnum" type="text" maxlength="2" onkeyup="if(event.keyCode !=37 && event.keyCode != 39){if (!/^[\d]+$/ig.test(this.value)){this.value='';}}">
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {
      inputnum: ''
    }
  },
  methods: {
    sendnum () {
      this.$emit('numinfo', this.inputnum)
    }
  },
  created () {
    // console.log(this.$store.getters.getcontainer)
    if (this.$store.getters.getisediting) {
      var questions = this.$store.getters.getquestions
      var id = this.$store.getters.geteditindex
      this.inputnum = questions[id].question.min
      this.$store.commit('savemin', this.inputnum)
      // console.log(this.inputnum)
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.numberinput
  display inline-block
.numberinput input
  display inline-block
  width 30px
  height 20px
  border 1px solid #dadada
  text-align center
  box-sizing border-box
</style>



