<template>
  <div class="publicpart">
    <div class="row">
      <label class="pubtitlelabel">题目</label>
      <Input v-model="topic" type="textarea" :autosize="true" placeholder="请输入题目"></Input>
    </div>
    <div class="row pubdescription">
      <label class="pubtitlelabel">备注</label>
      <Input v-model="remarks" type="textarea" :autosize="true" placeholder="请输入备注"></Input>
    </div>
    <div class="pubtype">
      <select class="selectclass" v-model="containertype">
        <option v-for="option in options" :value="option.value">{{ option.text }}</option>
      </select>
    <checkbox class="pubtypebox"></checkbox>
    <span class="pubtypeboxlabel">必填</span>
    </div>
    <keep-alive>
      <component :is="containertype" @childinfo="get" ref="c1" :class="{hidden: isbulkshow}"></component>
    </keep-alive>
    <bulk :isbulkshow="isbulkshow" @bulk-close="closebulk" @bulk-save="savebulk" ref="c2" :class="{hidden: !isbulkshow}"></bulk>
    <p v-if="containertype == 0 || containertype == 1 || containertype == 10 || containertype == 6" class="fulkbtn" @click="showbulk" :class="{hidden: isbulkshow}">批量修改(注意，批量修改会使当前输入的选项消失)</p>
    <div class="pubcontrol" :class="{hidden: isbulkshow}">
      <button class="btn bluebtn"  @click="savequestion">确定</button>
      <button class="btn whitebtn" @click="hidecontainer">取消</button>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import checkbox from '../checkbox.vue'
import choose from '../choose.vue'
import check from '../check.vue'
import singletext from '../singletext.vue'
import notsingletext from '../notsingletext.vue'
import star from '../star.vue'
import sort from '../sort.vue'
import dropdown from '../dropdown.vue'
import bulk from '../bulk/bulk'
export default {
  name: 'publicpart',
  data () {
    return {
      require: false,
      options: [
        {text: '单选题', value: '0'},
        {text: '多选题', value: '1'},
        {text: '下拉题', value: '10'},
        {text: '单行文本题', value: '3'},
        {text: '多行文本题', value: '4'},
        {text: '量表题', value: '5'},
        {text: '排序题', value: '6'}
      ],
      question: {},
      questions: {},
      isbulkshow: false
    }
  },
  components: {
    'checkbox': checkbox,
    '0': choose,
    '1': check,
    '3': singletext,
    '4': notsingletext,
    '5': star,
    '6': sort,
    '10': dropdown,
    'bulk': bulk
  },
  computed: {
    required () {
      return this.$store.getters.getisrequired
    },
    containertype: {
      get: function () {
        return this.$store.getters.getcontainertype
      },
      set: function (newValue) {
        this.$store.commit('changecontainertype', newValue)
      }
    },
    remarks: {
      get: function () {
        return this.$store.getters.getremarks
      },
      set: function (newValue) {
        this.$store.commit('changecontainerremark', newValue)
      }
    },
    topic: {
      get: function () {
        return this.$store.getters.gettopics
      },
      set: function (newValue) {
        this.$store.commit('changecontainertopic', newValue)
      }
    }
  },
  methods: {
    closebulk () {
      this.isbulkshow = !this.isbulkshow
    },
    showbulk () {
      this.isbulkshow = !this.isbulkshow
      this.$refs.c2.refresh()
    },
    savebulk () {
      this.isbulkshow = !this.isbulkshow
      this.$refs.c1.refresh()
    },
    get (options, editoptions, min, max, high) {
      this.container.options = options
      this.container.editoptions = editoptions
      this.min = min
      this.max = max
      this.high = high
    },
    hidecontainer () {
      this.$store.commit('stopediting')
      this.$store.commit('hidecontainer')
    },
    copyobj (obj) {
      var newobj = {}
      for (var key in obj) {
        var val = obj[key]
        newobj[key] = typeof (val) === 'object' ? this.copyobj(val) : val
      }
      return newobj
    },
    save () {
      if (!this.$store.getters.getisediting) {
        this.question = this.copyobj(this.$store.getters.getcontainer)
        this.questions = this.$store.getters.getquestions
        this.questions.push(this.question)
        this.$store.commit('savecontainer', this.questions)
      }
      if (this.$store.getters.getisediting) {
        this.$store.commit('stopediting')
        this.question = this.copyobj(this.$store.getters.getcontainer)
        this.$store.commit('saveedit', this.question)
      }
    },
    savequestion () {
      var questions = this.$store.getters.getcontainer
      var id = this.$store.getters.geteditindex
      var max = questions.question.max
      var min = questions.question.min
      // console.log(max,min)
      if (max < min) {
        this.$Notice.warning({
          title: '请检查设置'
        })
      }
      else if (this.$store.getters.gettopics === '') {
        this.$Notice.warning({
          title: '必须填写问题题目哦'
        })
      } else {
        // console.log(this.$store.getters.getcontainertype)
        // if (this.$store.getters.getcontainertype === 0 || this.$store.getters.getcontainertype === '0') {
        //   console.log(this.$store.getters.getoptions)
        //   if (this.$store.getters.getoptions === []) {
        //     console.log(222)
        //     this.$Notice.warning({
        //       title: '请添加选项'
        //     })
        //   } else {
        //     console.log('ok')
        //     this.save()
        //   }
        // }
        this.save()
      }
    }
  },
  created () {
    // console.log(this.$store.getters.getoptions)
    // console.log('hhh')
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.hidden
  display none
.row
  width 100%
  vertical-align middle
  display flex
  margin-bottom 10px
.pubtitlelabel
  min-width 40px
  line-height 30px
  height 30px
  text-align center
.selectclass:focus
  outline none
.selectclass
  height 22px
  border 1px solid #dadada
  color #505050
  margin-left 38px
.pubtypebox
  margin-left 50px
.pubtypeboxlabel
  font-size 14px
  position relative
  bottom 1px
  left 2px
.btn
  cursor pointer
  border-radius 4px
  padding 5px 20px
  border: 1px solid transparent
  font-size 12px
  box-sizing border-box
  background-color #f7f7f7
  border-color #dddee1
  color #495060
.btn:focus
  outline none
.bluebtn
  color #fff
  background-color rgba(0,161,233,0.79)
  border-color #2db7f5
.whitebtn
  color #495060
  background-color white
  border-color #dddee1
  transition color .2s linear,background-color .2s linear,border .2s linear
.whitebtn:hover
  color #2db7f5
  border-color #2db7f5
.pubcontrol
  text-align center
  margin-top 20px
.pubcontrol button
  margin 0 15px
.fulkbtn
  color #00a1e9
  cursor pointer
  margin-left 5px
</style>



