<template>
  <div class="checkboxwrapper">
    <span class="checkbox" @click="clickbox"  v-bind:class="{ checkedbox: istrue }">
       <input type="checkbox" id="checkbox1" class="checkboxhidden">
       <label for="checkbox1"></label>
     </span>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  // props: ['istrue'],
  data: function () {
    return {
      istrue: 0
    }
  },
  methods: {
    clickbox () {
      if (this.istrue) {
        this.istrue = 0
      } else {
        this.istrue = 1
      }
      // console.log(istrue)
      this.$store.commit('changeisrequired', this.istrue)
      // console.log(this.$store.getters.getisrequired)      
    }
  },
  created () {
    // console.log(this.$store.getters.getcontainer)
    if (this.$store.getters.getisediting) {
      var questions = this.$store.getters.getquestions
      var id = this.$store.getters.geteditindex
      this.istrue = questions[id].question.isrequired
      // console.log(this.istrue)
      this.$store.commit('changeisrequired', this.istrue)
    }
  }

}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.checkboxwrapper
  display inline-block
.checkboxhidden
  height 100%
  width 100%
  position absolute
  z-index 1
  cursor pointer
  opacity 0
.checkbox
  display inline-block
  height 14px
  width 14px
  position relative
  top 3px
  border 1px solid #dddee1
  border-radius 2px
  background-color #fff
  transition border-color .2s ease-in-out,background-color .2s ease-in-out
.checkbox label::after
  content ''
  opacity 1
  position absolute
  width 9px
  height 5px
  background transparent
  top 3px
  left 2px
  border 3px solid #505050
  border-top none !important
  border-right none !important
  transform rotate(-45deg)
  opacity 0
.checkbox:hover label::after
  opacity 0.5
.checkedbox label::after
  opacity 1
</style>



