<template>
  <div class="bulk">
    <div class="bulkwrapper" v-if="isbulkshow"></div>
    <div class="bulkcard">
      <Row>
          <Col span="8"  class-name="bulktitle">
            批量修改
          </Col>
<!--           <Col offset="15" span="1">
            <div class="cardclose" @click="closebulk">
              <Icon type="close"></Icon>
            </div>
          </Col>
 -->
      </Row>
      <div>
        <textarea class="optiontext" v-model="opts" placeholder="请输入选项列表"></textarea>
      </div>
      <p class="notice">请一行输入一个选项</p>
      <p class="notice">下拉题两级选项中间用'-'隔开，如：“天津-天津大学”</p>
      <p class="notice">注意，批量修改会覆盖此前输入的本题的选项</p>
      <div class="buttons">
        <button class="btn bluebtn"  @click="save">确定</button>
        <button class="btn whitebtn" @click="close">取消</button>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'bulk',
  props: {
    isbulkshow: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      opts: ''
    }
  },
  methods: {
    close () {
      this.$emit('bulk-close')
    },
    save () {
      // console.log(this.opts)
      var data = this.opts.split('\n')
      // console.log(data)
      var data1 = []
      for (var i=0; i<data.length; i++) {
        data1.push({option: data[i]})
      }
      this.$store.commit('saveoption', data1)
      // console.log(this.$store.getters.getoptions)
      this.$emit('bulk-save')
    },
    refresh () {
      this.opts = ''
    }
  },
  computed: {
  },
  created() {
    // var data = this.$store.getters.getoptions.join('\n')
    // console.log(data)
    // this.opts = data
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.drop-enter-active
  transition all .5s ease
.drop-leave-active
  transition all .5s ease
.drop-enter
  transform translateY(-1000px)
.drop-leave-active
  transform translateY(-1000px)
.bulk
  width 100%
  height 100%
// .bulkwrapper
//   position fixed
//   width 100%
//   height 100%
//   background-color #000
//   opacity 0.2
//   z-index 100
.bulkcard
  // width 400px
  // height 270px
  // position fixed
  // background-color white
  z-index 120
  padding 10px 5px
  // margin-left -200px
  // left 50%
  // top 200px
  // padding 25px
.cardclose
  color #707070
  font-size 15px
  cursor pointer
.bulktitle
  // font-size 15px
  // margin-bottom 30px
.btn
  cursor pointer
  border-radius 4px
  padding 5px 20px
  border: 1px solid transparent
  font-size 12px
  box-sizing border-box
  background-color #f7f7f7
  border-color #dddee1
  color #495060
  margin auto
.btn:focus
  outline none
.bluebtn
  color #fff
  background-color rgba(0,161,233,0.79)
  border-color #2db7f5
.whitebtn
  color #495060
  background-color white
  border-color #dddee1
  transition color .2s linear,background-color .2s linear,border .2s linear
.whitebtn:hover
  color #2db7f5
  border-color #2db7f5
.optiontext
  width 100%
  height 100px
  // margin auto
  outline none
  resize none 
  border 0 
  border-radius 5px
  overflow auto
  margin-top 5px 0
.notice
  font-size 12px
  color #ff9277
  margin-bottom 10px 0
.buttons
  text-align center
  margin-top 20px
.buttons button
  margin 0 15px  
</style>



