<template>
  <div class="singletext">
    <Row>
      <Col span="7">
      <div>
        <Select size="small" placeholder="文本验证" v-model="test">
          <Option v-for="item in townList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </div>
      </Col>
<!--       <Col span="10" offset="1">
        <span>最多输入</span>
        <numberinput @numinfo="getmaxword"></numberinput>
        <span>字</span>
      </Col>
 -->    </Row>
  </div>
</template>

<script type="text/ecmascript-6">
import numberinput from './numberinput3.vue'
export default {
  components: {
    'numberinput': numberinput
  },
  methods: {
    getmaxword (max) {
      this.$store.commit('savemax', max)
    }
  },
  data () {
    return {
      townList: [
        { value: '0', label: '不限' },
        { value: '1', label: '手机号' },
        { value: '2', label: '数字' },
        { value: '3', label: '日期' },
        { value: '4', label: '电子邮箱' },
        { value: '5', label: '中文' },
        { value: '6', label: '英文' },
        { value: '7', label: '网址' },
        { value: '8', label: '身份证号码' }
      ],
      test: '',
      max: ''
    }
  },
  watch: {
    test: function (val) {
      this.$store.commit('savetest', val)
    },
    max: function (val) {
      this.$store.commit('savemax', val)
    }
  },
  created () {
    // console.log(this.$store.getters.getcontainer)
    if (this.$store.getters.getisediting) {
      var questions = this.$store.getters.getquestions
      var id = this.$store.getters.geteditindex
      this.test = questions[id].question.test
      // this.max = questions[id].question.max
      // console.log(this.max)
    }
  }

}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.singletext
  margin-left 37px
  margin-top 18px
.goodinput
  display inline-block
  width 30px
  border 1px solid #dadada
</style>



