<template>
  <div class="surveylist">
  <!-- {{editid}} -->
  <div class="dd" v-for = "(list, index) in lists">
    <!-- {{index}}hhhh{{geteditindex()}} -->
    <!-- {{iscontainershow}} -->
    <!-- {{list}} -->
    <div @mouseover="show(index)" @mouseout="hide(index)" class="questionwrapper" v-if="index != editid" v-bind:class="{ activeqw: list.c }">
    <div class="clickactive">
      <div @click="del(index)" class="editbtn"></div>
      <div @click="edit(index, list)" class="delbtn"></div>
    </div>
    <div v-if="list.c" class="cover">
      <ul class="nav">
        <li>
          <img src="./del.png">
        </li>
        <li>
          <img src="./edit.png">
        </li>
      </ul>
    </div>
    <div>
      <div v-if="!list.isedit">
      <p class="questiontopic">{{index + 1}}.{{list.question.topic}}</p>
      <div class="questioninner">
        <p class="questionremark">{{list.question.remark}}</p>
        <div v-if = "list.question.qtype === '0'|| list.question.qtype === 0">
          <RadioGroup v-model="vertical" vertical>
            <Radio v-for ="(option, index) in list.options" :key="option.option">
              <span>{{option.option}}</span>
            </Radio>
          </RadioGroup>
        </div>

        <div v-if = "list.question.qtype === '1'||list.question.qtype === 1">
          <CheckboxGroup>
            <Checkbox :key="option.option" v-for ="(option, index) in list.options" style="display: block; margin-top:15px;" label="twitter">
              <span>{{option.option}}</span>
            </Checkbox>
          </CheckboxGroup>
        </div>

        <div v-if = "list.question.qtype === '3'||list.question.qtype === 3">
            <Input></Input>
        </div>

        <div v-if = "list.question.qtype === '4'||list.question.qtype === 4">
          <Input type="textarea" :rows="4"></Input>
        </div>

        <div v-if = "list.question.qtype === '5'||list.question.qtype === 5">
          <Row>
            <Col align="left" span="3"><span class="starlable">非常不{{list.question.stype}}</span></Col>
            <Col class-name="star" align="middle" span="18">
              <RadioGroup v-model="phone">
                <Radio :key="n" v-for="(n, index) in nums" v-if="index < list.question.srange" label="n">
                  <span>{{n}}</span>
                </Radio>
              </RadioGroup>
            </Col>
            <Col align="right" span="3"><span class="starlable">非常{{list.question.stype}}</span></Col>
          </Row>
        </div>

        <div v-if = "list.question.qtype === '6'||list.question.qtype === 6">
          <draggable :options="{sort:true, animation: 100}" @start="drag=true" @end="drag=false">
          <div class="move" v-for="option in list.options">
            <div class="moveborder">
              <span class="my-handle">
              <img src="./move.png">
              </span>
              {{option.option}}
            </div>
          </div>
          </draggable>
        </div>
        <div v-if = "list.question.qtype === '10'||list.question.qtype === 10">
          <!-- {{list.answer}} -->
          <!-- <Cascader :data="getcascaderdata(list.options)" trigger="hover"></Cascader> -->
          <i-select v-model.sync="list.answer.st" style="width: 49%; float: left; margin-right: 2%; max-heght:100px; overflow-y: auto;" @on-change="change1(list.answer)" clearable>
            <i-option v-for="(item1, index1) in getopt1(list.options)" :value="item1.value">{{item1.label}}</i-option>
          </i-select>
          <i-select style="width: 49%; float:right;" v-model.sync="list.answer.nd" clearable>
            <i-option v-for="(item2, index2) in getopt2(list)" :value="item2.value">{{item2.label}}</i-option>
          </i-select>
          <!-- <p>{{list.options}}</p> -->
          <!-- <p>{{select1}}</p> -->
        </div>

      </div>
      </div>
      <div>
     </div>
    </div>
  </div>
  <div v-if="index == editid" class="surveycontainer">
      <div class="setinner">
        <publicpart></publicpart>
      </div>
    </div>
  </div>
  </div>
</template>

<script type="text/ecmascript-6">
import draggable from 'vuedraggable'
import publicpart from '../publicpart/publicpart'
// import i-select from 'iview/src/components/select'
import iview from 'iview'
export default {
  components: {
    'draggable': draggable,
    'publicpart': publicpart
  },
  data () {
    return {
      nums: [1, 2, 3, 4, 5, 6, 7, 8, 9],
      vertical: 'apple',
      phone: '',
      phone1: '',
      phone11: '',
      phone111: '',
      select1: ''
    }
  },
  computed: {
    lists () {
      return this.$store.getters.getquestions
    },
    editid () {
      return this.$store.getters.geteditindex
    },
    iscontainershow () {
      return this.$store.getters.getcontainershow
    }
  },
  methods: {
    geteditindex () {
      // console.log(this.$store.getters.geteditindex)
      return this.$store.getters.geteditindex
    },
    del (index) {
      this.$store.commit('delquestion', index)
    },
    edit (index, list) {
      if (this.$store.getters.geteditindex != -1) {
        this.$Notice.warning({
          title: '请先完成当前问题'
        })
      }
      else {
        this.$store.commit('changecontainertype', list.question.qtype)
        this.$store.commit('startedit', index)
        this.$store.commit('startediting')
        this.$store.commit('changecontainertopic', list.question.topic)
        this.$store.commit('changecontainerremark', list.question.remark)
        this.$store.commit('updateoption', list.options)
        // console.log(this.$store.getters.getcontainershow)
        // console.log('sdfklj')
      }
    },
    show (index) {
      this.$store.commit('showcover', index)
    },
    hide (index) {
      this.$store.commit('hidecover', index)
    },
    getopt1 (options) {
      let data = []
      let n1 = -1
      for (let key in options) {
        let o = options[key].option.split('-')
        if ((n1 === -1) || (o[0] != data[n1].label)) { 
          n1++
          data[n1] = {
            value: o[0],
            label: o[0],
            children: []
          }
        }
      }
      return data      
    },
    getopt2 (q) {
      var options = q.options
      var answer = q.answer.st
      // console.log(answer)
      let data = []
      let n1 = -1
      for (let key in options) {
        let o = options[key].option.split('-')
        // console.log(o[0]+'djskflsd'+this.select1)
        if (o[0] == answer) { 
          n1++
          data[n1] = {
            value: answer + '-' +o[1],
            label: o[1]
          }
        }
      }
      return data      
    },
    change1 (answer) {
      answer.nd = ''
    }    
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.questionwrapper
  font-size 14px
  margin 15px auto
  padding-bottom 40px
  width 80%
  position relative
  z-index 50
  min-height 100px
  // overflow-y auto
.activeqw 
  z-index 100
.questionremark
  font-size 12px
  margin-bottom 13px
.questioninner
  margin 0 12px
.questiontype
  color #00a1e9
.upinput
  margin-bottom 10px
.ivu-input input
  height 26px !important
.starlable
  color #999
  font-size 11px
.move
  cursor move
  min-width 50%
.my-handle img
  width 20px
  position relative
  top 5px
.moveborder
  border 1px solid #dddee1
  height 32px
  width 100%
.tablerow
  border-bottom 1px solid #dddee1
  height 60px
.tablerow label
  padding-left 25px
.star label
  padding-left 25px
.listinput
  border-bottom 1px solid black
  display inline-block
.listinput:focus
  outline none
.nav
  position absolute
  height 30px
  bottom 0
  right 0
  z-index 100
  display flex
.nav li
  background-color white
  width 30px
  height 30px
  border 1px solid #dddee1
  text-align center
  vertical-align middle
.nav li img
  width 20px
  position relative
  top 3px
.cover
  width 100%
  height 100% !important
  border 1px solid #dddee1
  position absolute
  pointer-events none
.clickactive
  position absolute
  height 29px
  width 60px
  bottom 0px
  right 0px
  z-index 100
  cursor pointer
  display flex
.delbtn
  width 50%
  height 100%
.editbtn
  width 50%
  height 100%
.surveycontainer
  border 1px solid #dadada
  background-color rgba(233,234,238,0.52)
  margin 0 30px
  padding-top 10px
  padding-bottom 10px
  margin-bottom 20px
.setinner
  width 80%
  height 100%
  margin auto
  padding 10px
  box-sizing border-box
  min-height 20px
.table th,.table td
  padding 20px
  vertical-align middle
  text-align center
  border-bottom 1px solid #dedede
.questiontopic
  font-size 17px
.questionremark
  font-size 14px
  margin-left 10px
.table
  width 100%
</style>



