<template>
  <div class="editormain">
    <div class="surveywrap">
      <div class="surveymain">
        <div class="surveytitle wrapper">
          <div class="inner">
            <input v-model="title" maxlength="25" placeholder="问卷标题">
          </div>
        </div>
        <div class="surveyprefix wrapper">
          <div class="inner">
            <textarea v-model="prefix"></textarea>
          </div>
        </div>
        <div class="listwrapper">
          <div class="listinner">
            <editlist></editlist>
          </div>
        </div>
        <div v-if="iscontainershow" class="surveycontainer">
          <div class="setinner">
            <publicpart></publicpart>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import publicpart from '../publicpart/publicpart'
import editlist from '../editlist/editlist'
export default {
  components: {
    'publicpart': publicpart,
    'editlist': editlist
  },
  data: function () {
    return {
      fristsave: false
    }
  },
  computed: {
    // title () {
    //   return this.$store.getters.getname
    // },
    // prefix () {
    //   return this.$store.getters.getremark
    // },
    iscontainershow () {
      return this.$store.getters.getcontainershow
    },
    title: {
      get: function () {
        return this.$store.getters.getname
      },
      set: function (val) {
        this.$store.commit('changename', val)
      }
    },
    prefix: {
      get: function () {
        return this.$store.getters.getremark
      },
      set: function (val) {
        this.$store.commit('changeremark', val)
      }
    }
  }
  // watch: {
  //   title: function (val) {
  //     this.$store.commit('changename', val)
  //   },
  //   prefix: function (val) {
  //     this.$store.commit('changeremark', val)
  //   }
  // }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.editormain
  height 100%
  color #505050
.surveywrap
  margin-top 40px
  border: 1px solid #dadada
  background-color: #fff
  font-size: 14px
  text-align: left
  padding: 30px 0 30px
  margin auto 20px
  margin-top: 30px
  height 100%
  overflow-y auto
.surveymain
  margin-bottom 250px
.surveywrap input:focus
  outline 0
.surveywrap div:focus
  outline 0
.surveywrap input
  box-sizing border-box
  height 100%
  width 100%
  text-align center
  color #505050
.divtextarea
  margin 0
  border 0
  min-height 40px
  line-height 20px
  height 100%
  width 100%
  text-align left
  font-size 14px
  overflow hidden
.surveytitle
  height 75px
  font-size 23px
.surveytitle .inner
  padding 0
.wrapper
  margin 0 30px
  border 1px solid transparent
  padding-top 10px
  padding-bottom 10px
  margin-bottom 20px
.wrapper:hover
  border 1px solid #dadada
  background-color rgba(233,234,238,0.52)
.wrapper:hover .inner
  border 1px solid #dadada
.inner
  width 80%
  height 100%
  min-height 20px
  background-color white
  margin auto
  border 1px solid transparent
  box-sizing border-box
  padding 10px
.inner textarea
  height 100%
  width 100%
  border 1px solid transparent
  resize none
.inner textarea:hover
  outline none
.inner textarea:focus
  outline none
.setinner
  width 80%
  height 100%
  margin auto
  padding 10px
  box-sizing border-box
  min-height 20px
.surveycontainer
  border 1px solid #dadada
  background-color rgba(233,234,238,0.52)
  margin 0 30px
  padding-top 10px
  padding-bottom 10px
  margin-bottom 20px
.listwrapper
  margin 0 35px
.listinner
  width 100%
  margin 0 auto
  padding-bottom 30px
</style>



