<template>
  <div class="create">
   <surveyview :isviewshow="isviewshow" @view-close="closeview"></surveyview>
   <share :isshareshow="isshareshow" @share-close="closeshare"></share>
  <div class="createheader">
    <div class="createnav">
      <div class="createtext">编辑问卷</div>
      <div class="navright">
        <div @click="showshare" class="cimgwrapper">
          <img src="./plane.png">
          <span>发布</span>
        </div>
        <div class="cimgwrapper saveimg" @click="savequestionnaire">
        <img src="./save1.png">
          <span>保存</span>
        </div>
        <div class="cimgwrapper" @click="showview">
          <img src="./se.png">
          <span>预览</span>
        </div>
        <!-- <div>
          <button class="cbtn"><span>文本导入</span></button>
        </div> -->
      </div>
    </div>
  </div>
  <div class="content">
    <div class="content1">
      <div class="content1top">
        <!-- <router-link :to="{path: '/create/content1list'}"><button>题目控件</button></router-link><router-link :to="{path: '/create/bank'}"><button>题库</button></router-link> -->
        <button>题目控件</button>
      </div>
       <!-- <keep-alive>
        <router-view></router-view>
      </keep-alive> -->
      <content1list></content1list>
    </div>
    <div class="content2">
      <editormain></editormain>
    </div>
    <!-- <div class="content3"></div> -->
  </div>

  </div>
</template>

<script type="text/ecmascript-6">
import share from '../share/share'
import surveyview from '../surveyview/surveyview'
import editormain from '../editormain/editormain'
import content1list from '../content1list/content1list'
export default {
  data () {
    return {
      isviewshow: false,
      isshareshow: false,
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$http.get('loginStatus').then(response => {
        if (response.body.status === 0) window.location.href="http://survey.twtstudio.com/api/login"
        else {
          if (to.fullPath === '/create') {
            vm.$store.commit('emptyquestions')
            vm.$store.commit('emptyquestionnaire')
          }
          // console.log(vm.$route)
          if (vm.$route.name === 'edit') {
            // console.log(vm.$route.params.id)
            vm.$http.get('qnid/' + vm.$route.params.id).then((response) => {
              vm.$store.commit('editquestions', response.body.questions)
              vm.$store.commit('changename', response.body.questionnaire.name)
              console.log(vm.$store.getters.getquestions)
              console.log(response.body.questions)
            })
          }
        }
      })
    })
  },
  components: {
    'content1list': content1list,
    'editormain': editormain,
    'surveyview': surveyview,
    'share': share
  },
  methods: {
    showview () {
      this.isviewshow = !this.isviewshow
    },
    closeview () {
      this.isviewshow = !this.isviewshow
    },
    savequestionnaire () {
      if (this.$store.getters.getname === '') {
        this.$Notice.warning({
          title: '请填写问卷标题',
          desc: '填写完问卷标题，才能保存问卷哦！',
          top: 200
        })
        return
      }
      if (this.$store.getters.geteditindex != -1) {
        this.$Notice.warning({
          title: '请完成正在编辑的问题',
          desc: '完成正在编辑的问题，才能保存问卷哦！',
          top: 200
        })
        return
      }
      var list = this.$store.getters.getquestions
      var getquestionnaire = this.$store.getters.getbegin
      if (this.$route.path === '/create') {
        this.$http.post('status/0/edit', {questions: list, questionnaire: getquestionnaire}).then((response) => {
          this.$store.commit('renew')
          console.log(response.body)
            this.$store.commit('saveshareid', response.body.qnid)
            this.$store.commit('emptyquestions')
            this.$store.commit('emptyquestionnaire')
            this.$Message.success('保存成功啦！')
            setTimeout(() => {
              this.$router.push({path: '/edit/'+response.body.qnid})
            },2000);
        })
        .catch((err) => {
          this.$Notice.warning('保存失败，请稍后再试')
        })
      }
      console.log()
      if (this.$route.name === 'edit') {
        var id = this.$route.params.id
        this.$http.post('status/0/update/qnid/' + id, {questions: list, questionnaire: getquestionnaire}).then((response) => {
          this.$store.commit('renew')
            this.$store.commit('saveshareid', response.body.qnid)
            this.$store.commit('emptyquestions')
            this.$store.commit('emptyquestionnaire')
            this.$Message.success('保存成功啦！')
            this.$http.get('qnid/' + this.$route.params.id).then((response) => {
              this.$store.commit('editquestions', response.body.questions)
              this.$store.commit('changename', response.body.questionnaire.name)
            })
        })
        .catch((err) => {
          this.$Notice.warning('保存失败，请稍后再试')
        })
      }
    },
    showshare () {
      // var id = this.$store.getters.getid
      if (this.$store.getters.getname === '') {
        this.$Notice.warning({
          title: '请填写问卷标题',
          desc: '填写完问卷标题，才能发布问卷哦！',
          top: 200
        })
      }
      if (this.$store.getters.geteditindex != -1) {
        this.$Notice.warning({
          title: '请完成正在编辑的问题',
          desc: '完成正在编辑的问题，才能发布问卷哦！',
          top: 200
        })
        return
      }
      if (this.$store.getters.getname !== '') {
        var list = this.$store.getters.getquestions
        var getquestionnaire = this.$store.getters.getbegin
        if (this.$route.path === '/create') {
          // console.log('hhh')
          this.$http.post('status/1/edit', {questions: list, questionnaire: getquestionnaire}).then((response) => {
            // console.log('hhhhh')
            this.$store.commit('renew')
            this.$store.commit('saveshareid', response.body.qnid)
            // this.isshareshow = !this.isshareshow
            this.$store.commit('emptyquestions')
            this.$store.commit('emptyquestionnaire')
            this.isshareshow = !this.isshareshow
          })
          .catch((err) => {
            this.$Notice.warning('发布失败，请稍后再试')
          })
        }
        if (this.$route.name === 'edit') {
          this.$http.post('status/1/update/qnid/' + this.$route.params.id, {questions: list, questionnaire: getquestionnaire}).then((response) => {
            this.$store.commit('renew')
            this.$store.commit('saveshareid', response.body.qnid)
            // this.isshareshow = !this.isshareshow
            this.$store.commit('emptyquestions')
            this.$store.commit('emptyquestionnaire')
            this.isshareshow = !this.isshareshow
          })
          .catch((err) => {
            this.$Notice.warning('发布失败，请稍后再试')
          })
        }
      }
    },
    closeshare () {
      this.isshareshow = !this.isshareshow
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.create
  height 100%
.createheader
  height 70px
  position fixed
  left 0px
  right 0px
  top 60px
.createnav
  max-width 1000px
  height 70px
  margin 0 auto
  line-height 70px
.createtext
  color #505050
  clear both
  float left
  font-size 18px
  vertical-align middle
.navright div
  display inline-block
  height 70px
  width 130px
  float right
  vertical-align middle
.cbtn
  background-color rgba(0,161,233,0.79);
  border 0
  padding 2px
  width 100px
  height 30px
  color white
  vertical-align middle
.cbtn span
  position relative
  bottom 21px
.cbtn:focus
  outline none
.cimgwrapper
  cursor pointer
.cimgwrapper span
  vertical-align middle
  color #505050
  margin-left 17px
.cimgwrapper img
  width 30px
  vertical-align middle
  margin-left 30px
.saveimg img
  width 26px
.content
   display flex
   justify-content space-between
   width 100%
   height 100%
   overflow hidden
.content1,.content2,.content3
  box-sizing border-box
  position relative
  top 0
  margin-top 130px
  height calc(100% - 130px)
  border 1px solid rgba(0,0,0,0.1)
  background-color rgba(233,234,238,0.52)
.content1
  min-width 24%
  overflow-y auto
  padding-bottom 30px  
.content1 > div
  width 75%
  margin 0 auto
  white-space nowrap
.content1top button
  background-color rgba(0,161,233,0.79)
  color white
  width 100%
  border 0
  font-size 15px
  // background-color white
  // border 1px solid rgba(0,0,0,0.1)
  // width 51%
  margin 30px 0 20px 0
  height 40px
  padding 0
  cursor pointer
  // color #707070
  outline none
.content1top .router-link-active button
  background-color rgba(0,161,233,0.79)
  border 0
  color white
  outline 0
.content2
  width 100%
  margin-right 5px
  margin-left 5px
.content3
  min-width 24%
</style>



