<template>
  <div class="share" v-if="isshareshow">
    <div class="sharewrapper" @click="closeshare"></div>
    <div class="sharecard">
      <Row>
          <Col span="8"  class-name="sharetitle">
            发布
          </Col>
          <Col offset="15" span="1">
            <div class="cardclose" @click="closeshare">
              <Icon type="close"></Icon>
            </div>
          </Col>
      </Row>
      <Row>
          <Col span="4"  class-name="sharelabel">
            问卷链接
          </Col>
          <Col offset="1" span="14">
            <input v-model="link" type="text" class="shareinput">
          </Col>
          <Col offset="1" span="2" class-name="imgwrapper">
            <img src="./copy.png">
          </Col>
      </Row>
      <Row>
          <Col span="4"  class-name="sharelabel">
            二维码
          </Col>
          <Col offset="1" span="6">
            <div class="signwrapper">
              <img src="./sign.png">
            </div>
          </Col>
          <Col offset="5" span="6">
            <Row class-name="imgwrapper">

<!--               <Col span="8">
                <img src="./wechat.png">
              </Col>
 -->
<!--               <Col span="8">
                <img src="./wechatzone.png">
              </Col>
 -->
              <Col span="8">
                <img @click="shareQQ" src="./qq.png">
              </Col>
              <Col span="8">
                <img @click="shareqzone" src="./qqzone.png">
              </Col>

            </Row>

            <Row class-name="imgwrapper">
              
              <Col span="8" class-name="weiboimg">
                <img @click="shareweibo" src="./weibo.png">
              </Col>


<!--               <Col span="8" class-name="smallerimg">
                <img src="./massage.png">
              </Col>
 -->              
            </Row>
          </Col>
      </Row>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    isshareshow: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    closeshare () {
      this.$emit('share-close')
            // setTimeout(() => {
              this.$router.push({path: '/mine'})
            // },2000);
    },
    shareweibo () {
      window.open('http://v.t.sina.com.cn/share/share.php?appkey=&url=https://survey.twtstudio.com/answer/'+this.$store.getters.getshareid+'}}&title=分享问卷&&source=&sourceUrl=&content=&pic=')
    },
    shareQQ () {
      window.open('http://connect.qq.com/widget/shareqq/index.html?url=https://survey.twtstudio.com/answer/'+this.$store.getters.getshareid+'&title=分享问卷&source=&desc=&pics=&summary=快来填问卷吧')
    },
    shareqzone () {
      window.open('http://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?url=https://survey.twtstudio.com/answer/'+this.$store.getters.getshareid+'&title=&desc=&summary=快来填问卷吧&site=&pics=')
    },
    sharewechat () {
      window.open('http://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?url=https://survey.twtstudio.com/answer/'+this.$store.getters.getshareid+'&title=分享问卷&desc=&summary=快来填问卷吧&site=&pics=')
    }
  },
  computed: {
    link: function () {
      console.log('https://survey.twtstudio.com/answer/' + this.$store.getters.getshareid)
      return 'https://survey.twtstudio.com/answer/' + this.$store.getters.getshareid
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.drop-enter-active
  transition all .5s ease
.drop-leave-active
  transition all .5s ease
.drop-enter
  transform translateY(-1000px)
.drop-leave-active
  transform translateY(-1000px)
.share
  width 100%
  height 100%
.sharewrapper
  position fixed
  width 100%
  height 100%
  background-color #000
  opacity 0.2
  z-index 100
.sharecard
  width 400px
  height 270px
  position fixed
  background-color white
  z-index 120
  margin-left -200px
  left 50%
  top 200px
  padding 25px
.cardclose
  color #707070
  font-size 15px
  cursor pointer
.sharetitle
  font-size 18px
  margin-bottom 30px
.shareinput
  border 1px solid #dadada
  width 100%
.sharelabel
  font-size 13px
  margin-bottom 15px
.signwrapper
  border 1px solid #dadada
  width 100%
  padding 60%
  position realtive
.signwrapper img
  position absolute
  top 1px
  left 1px
  width 119%
  height 98%
.imgwrapper img
  width 100%
  position relative
  bottom 5px
  cursor pointer
.smallerimg
  width 20px
  position relative
  top 3px
  left 6px
.weiboimg
  width 30px
  position relative
  left 9px
  bottom 2px
</style>



